<template>
<div id="app">
  <div class='container-fluid' style="padding: 0px 0px 0px 0px">
    <Header img="" school='Go语言实战抽奖系统'></Header>
  </div>
  <div class="col-md-8 col-md-offset-2 mt-6" >
   <div class="reportTitle">
     <span>阶段学习训练反馈报告</span>
     <span v-show="reportIs"> —— </span>
     <span style="color: rgb(102, 18, 207)">{{dataList.stageName}}</span>
   </div>
    <div v-show="reportIs" class="reportContent">
      <div>
        <div class="xm-left-column-label">
          <span></span>
          <h1>效率情况</h1>
        </div>
        <div id="myChart" class="myChart" style="margin-top: 30px;"></div>
      </div>
      <div class="reportContentItem">
        <div class="xm-left-column-label">
          <span></span>
          <h1>质量情况</h1>
        </div>
        <div class="stepContent row" v-for="(obj, index) in dataList.qualityDetail" v-bind:key="index">
          <div class="col-md-2 stepContentTitle">{{obj.stepName}}</div>
          <div class="col-md-4"><span>最大相似度：</span>{{obj.similarity}}</div>
          <div class="col-md-4"><span>平均：</span>{{obj.similarity}}</div>
          <div class="col-xs-offset-2 col-md-4 stepContentContent"><span>提交次数：</span>{{obj.submitTimes}}</div>
          <div class="col-md-4 stepContentContent"><span>平均：</span>{{obj.submitTimes}}</div>
        </div>
      </div>
      <div class="reportContentItem">
        <div class="xm-left-column-label">
          <span></span>
          <h1>知识与技能情况</h1>
        </div>
        <div id="myChart3" class="myChart3"></div>
      </div>
      <div class="reportContentItem">
        <div class="xm-left-column-label">
          <span></span>
          <h1>在线学习情况</h1>
        </div>
        <div class="stepContent row">
          <div class="col-md-3 stepContentTitle">学习任务相关资料</div>
          <div class="col-md-4" ><span> {{learnTaskLength}}</span>小时</div>
          <div class="col-md-4"><span>超越：XX</span>人</div>
          <div class="col-md-3 stepContentTitle stepContentContent"> 学习项目知识点</div>
          <div class="col-md-4 stepContentContent"><span>{{learnDataLength}}</span>小时</div>
          <div class="col-md-4 stepContentContent" ><span>超越：XX</span>人</div>
        </div>
      </div>
      <div class="reportContentItem">
        <div class="xm-left-column-label">
          <span></span>
          <h1>沟通与交流情况</h1>
        </div>
        <div class="stepContent row">
          <div class="col-md-3 stepContentTitle">发表评论</div>
          <div class="col-md-4"><span>{{commentTimes}} </span>次</div>
          <div class="col-md-4"><span>超越：</span>人</div>
          <div class="col-md-3 stepContentTitle stepContentContent">回复评论</div>
          <div class="col-md-4 stepContentContent"><span>{{replyTimes}} </span>次</div>
          <div class="col-md-4 stepContentContent" ><span>超越：</span>人</div>
        </div>
      </div>
    </div>
    <div v-show="!reportIs" class="reportContent" style="text-align: center">报告暂无数据</div>
  </div>
</div>
</template>

<script>
import Header from '../../../components/school-training/Header.vue'
export default {
  name: 'StageReport',
  components: {
    Header
  },
  mounted () {
    this.initEvent()
    this.initData()
  },
  data () {
    return {
      winWidth: window.innerWidth,
      dataList: {},
      stepNameList: [],
      executionList: [],
      learnTaskLength: 0,
      learnDataLength: 0,
      commentTimes: 0,
      replyTimes: 0,
      name: [],
      probability: [],
      reportIs: true
    }
  },
  methods: {
    initEvent: function () {
      let me = this
      me.winWidth = $('body').width()
      window.onresize = function () {
        me.winWidth = $('body').width()
      }
    },
    initData: function () {
      let me = this
      me.$http.post('/doReport/stageReportInfo', {
        'stageId': localStorage.getItem('stageId')
      }, res => {
        me.dataList = res.data
        if (res.data.stageName === null) {
          me.reportIs = false
        } else {
          if (res.data.executionDetail !== null) {
            for (var i=0; i < res.data.executionDetail.length; i++) {
              me.stepNameList.push(res.data.executionDetail[i].stepName)
              me.executionList.push(res.data.executionDetail[i].execution)
            }
          }
          if (res.data.onlineLearnDetail !== null) {
            for (var j = 0; j < res.data.onlineLearnDetail.length; j++) {
              me.learnDataLength += res.data.onlineLearnDetail[j].learnDataLength
              me.learnTaskLength += res.data.onlineLearnDetail[j].learnTaskLength
            }
          }
          if (res.data.discussionDetail !== null) {
            for (var z = 0; z < res.data.discussionDetail.length; z++) {
              me.commentTimes += res.data.discussionDetail[z].commentTimes
              me.replyTimes += res.data.discussionDetail[z].replyTimes
            }
          }
          if (res.data.knowledgeSkillDetail[0].knowledgeSkill !== null) {
            for (var a = 0; a < JSON.parse(res.data.knowledgeSkillDetail[0].knowledgeSkill).length; a++) {
              me.name.push(JSON.parse(res.data.knowledgeSkillDetail[0].knowledgeSkill)[a].name)
              // me.probability.push(JSON.parse(res.data.knowledgeSkillDetail[0].knowledgeSkill)[a].probability)
              me.probability.push(JSON.parse(res.data.knowledgeSkillDetail[0].knowledgeSkill)[a].value)
            }
          }
        }

        me.$nextTick(function () {
          me.document = document.getElementById('myChart')
          me.document3 = document.getElementById('myChart3')
          me.drawLine()// => 'updated'
        })
      })
    },
    drawLine () {
      // 基于准备好的dom，初始化echarts实例
      var myChart = this.$echarts.init(this.document)
      // 绘制图表
      myChart.setOption({
        title: {
          // text: '世界人口总量',
          // subtext: '数据来自网络'
        },
        tooltip: {
          trigger: 'axis'
        },
        legend: {
          // data:['2011年']
        },
        toolbox: {
          show: true,
          feature: {
          }
        },
        calculable: true,
        xAxis: [
          {
            type: 'value',
            boundaryGap: [0, 0.01]
          }
        ],
        yAxis: [
          {
            type: 'category',
            // data: ['任务1', '任务2', '任务3', '任务4']
            data: this.stepNameList
          }
        ],
        series: [
          {
            name: '完成情况',
            type: 'bar',
            data: this.executionList
            // data: [82, 48, 90, 100]
          }
          // {
          //   name: '2012年',
          //   type: 'bar',
          //   data: [19325, 23438, 31000, 121594, 134141, 681807]
          // }
        ]
      })
      // 基于准备好的dom，初始化echarts实例
      var myChart3 = this.$echarts.init(this.document3)
      // 绘制图表
      myChart3.setOption({
        title: {
          // text: '动态数据',
          // subtext: '纯属虚构'
        },
        tooltip: {
          trigger: 'axis',
          axisPointer: {
            type: 'cross',
            label: {
              backgroundColor: '#283b56'
            }
          }
        },
        legend: {
          data: ['平均分', '得分']
        },
        dataZoom: {
          show: false,
          start: 0,
          end: 100
        },
        xAxis: [
          {
            type: 'category',
            boundaryGap: true,
            data: this.name
          },
          {
            type: 'category',
            boundaryGap: true,
            data: this.name
          }
        ],
        yAxis: [
          {
            type: 'value',
            scale: true,
            name: '分数',
            max: 100,
            min: 0,
            boundaryGap: [0.2, 0.2]
          },
          {
            type: 'value',
            scale: true,
            name: '分数',
            max: 100,
            min: 0,
            boundaryGap: [0.2, 0.2]
          }
        ],
        series: [
          {
            name: '得分',
            type: 'bar',
            xAxisIndex: 1,
            yAxisIndex: 1,
            data: this.probability
          },
          {
            name: '平均分',
            type: 'line',
            data: (function () {
              var res = []
              var len = 0
              while (len < 6) {
                res.push((Math.random() * 10 + 5).toFixed(1) - 0)
                len++
              }
              return res
            })()
          }
        ]
      })
    }
  }
}
</script>

<style scoped>
  .reportTitle {
    text-align: center;
    font-size:40px;
  }
  .reportTitle span{
    font-size:40px;
  }
  .reportContent {
    background: white;
    -webkit-box-shadow: 0px 3px 15px 0px rgba(0, 0, 0, 0.08);
    box-shadow: 0px 3px 15px 0px rgba(0, 0, 0, 0.3);
    border-radius: 5px;
    padding: 20px 50px 50px 50px;
    margin-top: 60px;
    margin-bottom: 50px;
  }
  .reportContentItem {
    margin-top: 60px
  }
  .myChart {
    margin:auto;
    min-width: 500px;
    min-height: 300px;
  }
  .myChart3 {
    margin:auto;
    min-width: 500px;
    min-height: 500px;
    margin-top: 50px
  }
  .xm-left-column-label {
    height: 18px;
    line-height: 18px;
    position: relative;
    font-size: 16px;
    color: #000;
    padding: 0 0 14px 11px;
    margin-top: 10px;
  }
  .xm-left-column-label span {
    display: block;
    height: 30px;
    width: 3px;
    background: #fe0000;
    position: absolute;
    top: 0;
    left: 0;
  }
  .stepContent {
    margin-left: 50px;
    margin-top: 50px;
    font-size: 20px;
  }
  .stepContentTitle {
    color: rgb(102, 18, 207);
    font-size: 20px;
  }
  .stepContentContent {
    margin-top: 20px
  }
</style>
